.pipeline-node-list-search {
  padding: 14px 12px 12px; }
  .kui-theme--light .pipeline-node-list-search {
    background: #f0f0f0; }
  .kui-theme--dark .pipeline-node-list-search {
    background: #122531; }
  .pipeline-node-list-search .kui-searchbar,
  .pipeline-node-list-search .kui-input-wrapper {
    width: 100%; }

.pipeline-node-list-scrollbars {
  min-height: 400px; }
  .kui-theme--light .pipeline-node-list-scrollbars {
    background: #f0f0f0; }
  .kui-theme--dark .pipeline-node-list-scrollbars {
    background: #122531; }

.pipeline-node-list__toggle-title {
  margin: 0 24px 12px;
  font-size: 1.8em;
  font-weight: 600; }

.pipeline-node-list__toggle-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 12px 24px;
  padding: 10px 0; }
  .kui-theme--light .pipeline-node-list__toggle-container {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd; }
  .kui-theme--dark .pipeline-node-list__toggle-container {
    border-top: 1px solid #081c28;
    border-bottom: 1px solid #081c28; }

.pipeline-node-list__toggle {
  background: none;
  padding: 2px 0 1px;
  box-shadow: none;
  border: none;
  color: inherit;
  font-size: 1.6em;
  cursor: pointer;
  opacity: 0.8;
  outline: none;
  border-bottom: 1px solid transparent; }
  .pipeline-node-list__toggle:focus {
    border-bottom-width: 1px;
    border-bottom-style: solid; }
    .kui-theme--light .pipeline-node-list__toggle:focus {
      border-bottom-color: rgba(0, 0, 0, 0.5); }
    .kui-theme--dark .pipeline-node-list__toggle:focus {
      border-bottom-color: rgba(255, 255, 255, 0.5); }
  .pipeline-node-list__toggle:hover, .pipeline-node-list__toggle:focus {
    opacity: 1; }

.pipeline-node-list__icon {
  margin: 0 4px -6px -6px;
  fill: white; }
  .kui-theme--light .pipeline-node-list__icon {
    fill: rgba(0, 0, 0, 0.9); }
  .kui-theme--dark .pipeline-node-list__icon {
    fill: white; }
  .pipeline-node-list__icon--uncheck {
    transform: scale(0.75); }

.pipeline-node-list {
  margin: 0 12px;
  padding: 0;
  list-style: none; }

.pipeline-node {
  transition: opacity ease 0.4s; }
  .pipeline-node .kui-switch {
    margin: 0; }
    .pipeline-node .kui-switch label {
      max-width: 100%;
      padding: 12px;
      text-align: left; }
      .pipeline-node .kui-switch label span {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: calc(100% - 35px); }
        .pipeline-node .kui-switch label span b {
          opacity: 0.3; }

.pipeline-node:hover,
.pipeline-node--active {
  background: rgba(255, 255, 255, 0.1); }

.pipeline-node--disabled {
  opacity: 0.2;
  pointer-events: none; }
