.kedro-pipeline .kui-switch {
  position: relative; }

.kedro-pipeline .kui-switch-radio__box {
  min-width: 20px; }

.kedro-pipeline.kui-theme--dark .kui-switch__input:focus + .kui-switch-radio__label .kui-switch-radio__box, .kedro-pipeline.kui-theme--light .kui-switch__input:focus + .kui-switch-radio__label .kui-switch-radio__box {
  box-shadow: 0 0 0 4px rgba(0, 191, 255, 0.6); }

.kedro-pipeline button {
  font-family: inherit; }

.pipeline-icon-button {
  background: none;
  border: none;
  padding: 4px;
  margin: 0;
  box-shadow: none;
  cursor: pointer;
  border-radius: 50%;
  font-size: 1em;
  width: 3.2em;
  height: 3.2em;
  transition: all ease 0.1s;
  transform: scale(1.3); }
  .pipeline-icon-button:hover {
    transform: scale(1.5); }
  .pipeline-icon-button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(0, 191, 255, 0.6); }
