.snapshot-description {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px 26px 0;
  text-align: right;
  max-width: 90%;
  transition: max-width ease 0.4s; }
  .snapshot-description--menu-visible {
    max-width: 70%;
    max-width: calc(100% - 400px); }
  .snapshot-description p {
    font-size: 1.6em;
    margin: 3px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }
    .kui-theme--light .snapshot-description p {
      color: rgba(29, 47, 58, 0.55); }
    .kui-theme--dark .snapshot-description p {
      color: rgba(255, 255, 255, 0.55); }
  .snapshot-description b {
    font-weight: 600; }
    .kui-theme--light .snapshot-description b {
      color: rgba(29, 47, 58, 0.85); }
    .kui-theme--dark .snapshot-description b {
      color: rgba(255, 255, 255, 0.85); }
