.kedro-pipeline {
  position: relative;
  font-size: 10px;
  height: 100%;
  overflow: hidden; }
  .kedro-pipeline.kui-theme--light {
    background: white;
    color: #333; }
  .kedro-pipeline.kui-theme--dark {
    background: #1d2f3a;
    color: #fff; }

.pipeline-wrapper {
  height: 100%; }

.pipeline-sidebar {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  transition: transform ease 0.4s;
  width: 100%;
  transform: translateX(-100%);
  padding-top: 12px; }
  .pipeline-sidebar .pipeline-ui {
    visibility: hidden;
    transition: visibility 0s; }
  @media (min-width: 480px) {
    .pipeline-sidebar {
      width: 300px;
      transform: translateX(-300px); } }
  .pipeline-sidebar--visible {
    transform: translateX(0); }
    @media (min-width: 480px) {
      .pipeline-sidebar--visible {
        transform: translateX(0); } }
    .pipeline-sidebar--visible .pipeline-ui {
      visibility: visible;
      transition: visibility 0.3s; }
  .kui-theme--light .pipeline-sidebar {
    background: #f9f9f9;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.07); }
  .kui-theme--dark .pipeline-sidebar {
    background: #111f2a;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); }

.pipeline-sidebar__show-menu {
  position: absolute;
  top: 20px;
  left: 20px;
  transition: all ease 0.4s; }

.pipeline-sidebar__hide-menu {
  position: absolute;
  top: 20px;
  right: 20px;
  visibility: hidden;
  opacity: 0;
  transition: all ease 0.4s; }
  .pipeline-sidebar__hide-menu--offset {
    right: -50px; }
  .pipeline-sidebar__hide-menu--visible {
    visibility: visible;
    opacity: 1; }

.kui-theme--light .menu-icon {
  fill: #333; }

.kui-theme--dark .menu-icon {
  fill: #fff; }
